import { Controller } from '@ali/4ever-cangjie';
import { LinkPluginConfig } from '../types';
import { LinkPlugin } from '@ali/4ever-bamboo';

const { normalizeLink, openLink } = LinkPlugin;

export default function createOpenLink(
  protocolAllowList: Required<LinkPluginConfig>['protocolAllowList'],
  openLinkOfConfig?: LinkPluginConfig['openLink'],
) {
  return function (
    controller: Controller,
    href: string,
  ) {
    if (openLinkOfConfig) {
      const normalizedLink = normalizeLink(href, protocolAllowList);
      openLinkOfConfig(normalizedLink);
    } else {
      openLink(href, protocolAllowList);
    }
    return controller;
  };
}
